/*穿透表格组件，设置表格内容不溢出*/
.ant-table-wrapper /deep/ .ant-table table {
  width: 100%;
  /*必须设置，否则还是会拉长单元格*/
  table-layout: fixed;
  /*只有定义表格的算法为fixed,th才有效果*/
  word-wrap: break-all;
}
.ant-table-wrapper /deep/ .ant-table th > div,
.ant-table-wrapper /deep/ .ant-table td {
  overflow: hidden;
  /*超出长度的文字隐藏*/
  text-overflow: ellipsis;
  /*文字隐藏以后添加省略号*/
  white-space: nowrap;
  /*强制不换行*/
  word-break: keep-all;
  /*文字不换行*/
}
